html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.container {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: auto;
  padding: 0 50px;
  display: flex;
}

.header .logo, .header .logo img {
  display: block;
}

.header .nav__list {
  display: flex;
}

.header .nav__item + .nav__item {
  margin-left: 20px;
}

.header .nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.header .nav__link[data-qa="nav-hover"]:hover {
  color: #00acdc;
}

.header .nav__link.is-active {
  color: #00acdc;
  position: relative;
}

.header .nav__link.is-active:before {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.main {
  box-sizing: border-box;
  grid-template-rows: auto;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  object-fit: cover;
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.card__wrap-stars-reviews {
  justify-content: space-between;
  align-items: end;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__wrap-price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price-text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__order-btn {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  padding: 11px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: block;
}

.card__order-btn:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}
